@font-face {
    font-family: 'GongGothicMedium' ;
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-10@1.0/GongGothicMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
body{
    background-color: black;
}
h1{
    display: flex;
    justify-content: center;
    align-items: center;
}
header{
    margin: 10px 0px;
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    /* background-color: yellowgreen; */
}
header>span:first-child{
    flex:1;
    margin-left: 20px;
    color: #FF0558;
    font-family: 'GongGothicMedium';
    font-size: 25px;
    font-weight: 800;
}
header>img{
    margin: 10px;
    display: flex;
    /* background-color: aqua; */
    align-items: center;
    justify-content: center;
    width: 40px;
    height: 40px;
    border-radius: 100%;
    cursor: pointer;
}
main{
    padding: 20px;
    /* background-color: aqua; */
    display: flex;
    justify-content: space-evenly; 
    flex-wrap: wrap;
}

