@font-face {
    font-family: 'GongGothicMedium' ;
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_20-10@1.0/GongGothicMedium.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}
*{
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
h1{
    color: antiquewhite;
    font-family: "GongGothicMedium";
}
#coverImg{
    width: 100%;
    height: 100%;
    min-width: 150px;
    min-height: 200px;
}
.box {
    width: 50%;
    min-width: 300px;
    padding: 15px;
    margin: 20px;
    border-radius: 5px;
    background-color: white;
    box-shadow: 5px 5px 10px grey;
    /* 한 줄에 최대 두 콘텐츠만 배치하는 방법 찾기 */
    display: flex; 
}
.box:hover{
    transform: scale(1.1);
    transition: transform ease-in-out .3s;
}
.content{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    width: 100%;
}
p{
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 5;
    -webkit-box-orient: vertical;
    
}
ul{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0px;
    list-style:none;
}
li{
    margin: 0 10px;
}
